    display: none;
    flex-direction: column;
    padding: 93px 15px 65px;
    color: #011C40;
    background-color: #F9FBFD;
    max-width: 100%;
  }
  .main-nav--footer {
    display: flex;
    margin: 0;
    padding: 0;
    margin-bottom: 26px;
    background-color: inherit;
  }
  .main-nav__item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 30px;
  }
  .main-nav__item--footer:not(:last-child) {
    margin-bottom: 13px;
  }
  .main-nav__link {
    font-size: 18px;
    line-height: 22px;
    color: #011C40;
  }
  .main-nav__link--footer {
    font-size: 12px;
    line-height: 15px;
    color: #F9FBFD;
  }
}
